/* neon */
@keyframes neon-once {
  50% {
    opacity: 1;
    fill: #87ff00;
    filter: drop-shadow(0 0 10px #539c00); } }

@keyframes neon_on {
  0% {
    opacity: 0.3;
    text-shadow: 0 0 15px #539c00; }
  30%,
  80% {
    opacity: 1;
    color: white;
    text-shadow: 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #87ff00, 0 0 25px #87ff00, 0 0 40px #87ff00; }
  35%,
  45%,
  55%,
  65%,
  75% {
    opacity: 0.3;
    color: #87ff00;
    text-shadow: 0 0 15px #539c00; }
  40%,
  50%,
  60% {
    opacity: 0.8;
    color: #87ff00;
    text-shadow: 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #87ff00, 0 0 25px #87ff00, 0 0 40px #87ff00; }
  100% {
    opacity: 1;
    color: #87ff00;
    text-shadow: 0 0 5px #fff, 0 0 10px #87ff00, 0 0 20px #87ff00; } }

@keyframes neon-border {
  0%,
  30%,
  60%,
  85% {
    opacity: 0.8;
    border: 3px solid #539c00;
    box-shadow: 0 0 2px 1px #539c00, inset 0 0 2px 1px #539c00; }
  90% {
    opacity: 0.8;
    border: 3px solid white; }
  15%,
  45%,
  75%,
  100% {
    box-shadow: 0 0 2px 1px #87ff00, inset 0 0 2px 1px #87ff00;
    border: 3px solid #87ff00;
    opacity: 1; } }

/* mask */
@keyframes mask-after {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(101%); } }

@keyframes clip {
  from {
    opacity: 0.1; }
  to {
    opacity: 1;
    filter: blur(0px); } }

/* fade-in */
@keyframes fade-in-right {
  from {
    opacity: 0;
    transform: translateX(1em); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes fade-out-left {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(1em); } }

/* pulse */
@keyframes pulse {
  from {
    transform: scale(1.1); }
  to {
    transform: scale(1); } }

@font-face {
  font-family: 'Neon_R';
  src: url("./styles/font/TextMeOne-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'BBTreeGL';
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_nine_@1.1/BBTreeGL.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

body,
html,
#root {
  margin: 0;
  display: flex;
  width: 100vw;
  height: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  resize: none;
  font-size: 30px;
  background-color: #212529;
  font-family: 'BBTreeGL', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: white;
  cursor: default; }
  @media (min-width: 2000px) {
    body,
    html,
    #root {
      font-size: 45px; } }
  @media (max-width: 1254px) {
    body,
    html,
    #root {
      font-size: 28px; } }
  @media (max-width: 959px) {
    body,
    html,
    #root {
      font-size: 20px; } }
  @media (max-width: 689px) {
    body,
    html,
    #root {
      font-size: 15px; } }
  @media (max-width: 499px) {
    body,
    html,
    #root {
      font-size: 10px; } }

.wrapper {
  height: 100vh;
  overflow-y: scroll;
  scroll-behavior: smooth; }

*::-webkit-scrollbar {
  display: none !important; }

* {
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none; }

.modal {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 11;
  background-color: #212529;
  position: absolute;
  left: 0; }

ul {
  list-style-type: none;
  padding-left: 0.8em; }

li::before {
  font-family: Neon_R;
  color: #87ff00;
  font-size: 1.5em;
  content: '.'; }
