@charset "UTF-8";
/* neon */
@keyframes neon-once {
  50% {
    opacity: 1;
    fill: #87ff00;
    filter: drop-shadow(0 0 10px #539c00); } }

@keyframes neon_on {
  0% {
    opacity: 0.3;
    text-shadow: 0 0 15px #539c00; }
  30%,
  80% {
    opacity: 1;
    color: white;
    text-shadow: 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #87ff00, 0 0 25px #87ff00, 0 0 40px #87ff00; }
  35%,
  45%,
  55%,
  65%,
  75% {
    opacity: 0.3;
    color: #87ff00;
    text-shadow: 0 0 15px #539c00; }
  40%,
  50%,
  60% {
    opacity: 0.8;
    color: #87ff00;
    text-shadow: 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #87ff00, 0 0 25px #87ff00, 0 0 40px #87ff00; }
  100% {
    opacity: 1;
    color: #87ff00;
    text-shadow: 0 0 5px #fff, 0 0 10px #87ff00, 0 0 20px #87ff00; } }

@keyframes neon-border {
  0%,
  30%,
  60%,
  85% {
    opacity: 0.8;
    border: 3px solid #539c00;
    box-shadow: 0 0 2px 1px #539c00, inset 0 0 2px 1px #539c00; }
  90% {
    opacity: 0.8;
    border: 3px solid white; }
  15%,
  45%,
  75%,
  100% {
    box-shadow: 0 0 2px 1px #87ff00, inset 0 0 2px 1px #87ff00;
    border: 3px solid #87ff00;
    opacity: 1; } }

/* mask */
@keyframes mask-after {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(101%); } }

@keyframes clip {
  from {
    opacity: 0.1; }
  to {
    opacity: 1;
    filter: blur(0px); } }

/* fade-in */
@keyframes fade-in-right {
  from {
    opacity: 0;
    transform: translateX(1em); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes fade-out-left {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(1em); } }

/* pulse */
@keyframes pulse {
  from {
    transform: scale(1.1); }
  to {
    transform: scale(1); } }

.title {
  font-family: 'Neon_R';
  color: #539c00;
  text-shadow: 0 0 15px #539c00;
  font-weight: bold;
  animation: neon_on 1.3s none ease-out 0.5s;
  color: #87ff00;
  text-shadow: 0 0 10px #87ff00;
  font-size: 1.5em; }
  @media (max-width: 1254px) {
    .title {
      font-size: 1.2em; } }

.value {
  font-size: 1.4em; }
  @media (max-width: 1254px) {
    .value {
      font-size: 1em; } }

.top {
  display: flex;
  width: 100%;
  height: 40vh;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 10vh; }

.bottom {
  display: flex;
  width: 100%;
  height: 60vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 10vh; }
  .bottom .skill {
    display: flex;
    width: 100%;
    height: 80%;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
    .bottom .skill text :hover {
      cursor: pointer; }

.chart,
.detail {
  display: flex;
  width: 40%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.chart {
  align-items: flex-end; }
  .chart div {
    /* inline 속성으로 인하여 important사용 */
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90% !important;
    height: 100% !important; }
    .chart div svg {
      width: 100%;
      height: 100%; }

.detail {
  justify-content: flex-start;
  animation: fade-in-right 1s forwards; }
  .detail .score {
    font-size: 1.7em;
    margin-top: 0.7em; }

.info {
  display: flex;
  width: 100%;
  height: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-content: flex-start;
  margin-right: 0.8em; }
  .info div {
    display: flex;
    width: 100%;
    height: auto;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
    .info div span {
      margin-left: 1em; }
  .info .title {
    width: 20%;
    text-align: right; }
  .info .value {
    text-align: left;
    width: 40%; }

.career {
  display: flex;
  width: 100%;
  height: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 3; }
  .career .title {
    width: 100%;
    align-items: flex-start; }
  .career ul {
    width: 100%;
    margin: 0.2em 0px auto; }
  .career li {
    font-size: 1.3em; }
    @media (max-width: 1254px) {
      .career li {
        font-size: 1.2em; } }

/* arrow */
.arrow {
  display: flex;
  width: 30%;
  height: 20%;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: 2em;
  margin-bottom: 2em; }
  .arrow svg {
    flex-grow: 1;
    opacity: 0.3;
    fill: #539c00;
    filter: drop-shadow(0 0 15px #539c00);
    animation-name: neon-once;
    animation-duration: 0.8s;
    animation-iteration-count: infinite; }
  .arrow svg:nth-child(1) {
    animation-delay: 0.6s; }
  .arrow svg:nth-child(2) {
    animation-delay: 0.4s; }
  .arrow svg:nth-child(3) {
    animation-delay: 0.2s; }
